body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
}

h2 {
  font-family: "Raleway", sans-serif;
  font-size: 2.25rem;
}

.container {
  max-width: 1325px;
  margin: 0 auto;
  padding: 0 0.9375rem;
}
.container .row {
  display: flex;
  flex-wrap: wrap;
}

[class*=col-] {
  min-height: 1px;
}
@media only screen and (max-width: 795px) {
  [class*=col-] {
    width: 100%;
  }
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 10;
  height: 70px;
  box-shadow: 0 3px 6px rgba(0, 128, 0, 0.26);
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1325px;
  height: 100%;
  background-color: #818181;
}
.header__container .nav {
  background-color: #F7F7F7;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 2em;
  position: relative;
}
@media only screen and (max-width: 795px) {
  .header__container .nav__link {
    color: #08a6e4;
  }
}
.header__container .nav .logo {
  margin-left: 2px;
  display: flex;
  align-items: center;
  width: 30%;
}
.header__container .nav .logo__rigo {
  border-radius: 50%;
  border: solid 2px #818181;
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("../assets/swan-male.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.header__container .nav .logo__string {
  font-family: "Raleway", sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  color: #08a6e4;
  margin-left: 2px;
}
.header__container .nav__buttons {
  display: flex;
  width: 55%;
  justify-content: space-around;
}
@media only screen and (max-width: 795px) {
  .header__container .nav__buttons {
    position: absolute;
    right: 0;
    top: 75px;
    background-color: #e1e2e3;
    height: 20vh;
    flex-direction: column;
    text-align: center;
    width: 20%;
    transform: translate(100%);
  }
  .header__container .nav__buttons--active {
    transform: translate(0%);
    z-index: 5;
  }
}
.header__container .nav__element a {
  position: relative;
  padding: 0.2em;
  font-size: 1rem;
  letter-spacing: 2px;
  font-weight: bold;
}
.header__container .nav__element a:hover {
  background-color: #e1e2e3;
  border-radius: 20px;
}
.header__container .nav__element a:after {
  content: "";
  position: absolute;
  bottom: -0.8rem;
  left: 0;
  background-color: #08a6e4;
  height: 0.15em;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
}
.header__container .nav__element a:hover::after {
  width: 100%;
}
.header__container .nav__hamburger {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  display: none;
}
.header__container .nav__hamburger--line {
  width: 3em;
  height: 0.3em;
  background-color: #08a6e4;
  margin: 0.7em;
  border-radius: 20px;
  transition: 0.3s ease-in-out;
}
@media only screen and (max-width: 795px) {
  .header__container .nav__hamburger {
    display: block;
    transition: 0.3s ease-in-out;
  }
  .header__container .nav__hamburger--active .nav__hamburger--line:first-child {
    transform: translateY(-999px);
  }
  .header__container .nav__hamburger--active .nav__hamburger--line:nth-child(2) {
    transform: rotate(-405deg) translateY(10px);
  }
  .header__container .nav__hamburger--active .nav__hamburger--line:nth-child(3) {
    transform: rotate(45deg) translateY(-10px);
  }
}