.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 10;
  height: 70px;
  box-shadow: 0 3px 6px $color-primary-shadow;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: $containerWidth;
    height: 100%;
    background-color: $color-grey;
    .nav {
      background-color: $color-light-grey;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%;
      padding: 2em;
      position: relative;
      &__link {
        @include mobile {
          color: $color-primary;
        }
      }

      .logo {
        margin-left: 2px;
        display: flex;
        align-items: center;
        //height: 100%;
        width: 30%;

        &__rigo {
          border-radius: 50%;
          border: solid 2px $color-grey;
          width: 2.5rem;
          height: 2.5rem;
          background-image: url("../assets/swan-male.jpg");
          background-size: cover;
          background-repeat: no-repeat;
        }

        &__string {
          font-family: $font-raleway;
          font-size: 1.5rem;
          font-weight: 600;
          color: $color-logo-string;
          margin-left: 2px;
        }
      }
      &__buttons {
        @include mobile {
          position: absolute;
          right: 0;
          top: 75px;
          background-color: $color-grey-medium;
          height: 20vh;
          flex-direction: column;
          text-align: center;
          width: 20%;
          transform: translate(100%);
          &--active {
            transform: translate(0%);
            z-index: 5;
          }
        }
        display: flex;
        width: 55%;
        justify-content: space-around;
      }
      &__element {
        a {
          &:hover {
            background-color: $color-grey-medium;
            border-radius: 20px;
          }
          position: relative;
          padding: 0.2em;
          font-size: 1rem;
          letter-spacing: 2px;
          font-weight: bold;

          &:after {
            content: "";
            position: absolute;
            bottom: -0.8rem;
            left: 0;
            background-color: $color-primary;
            height: 0.15em;
            border-radius: 10px;
            transition: 0.3s ease-in-out;
          }
          &:hover::after {
            width: 100%;
          }
        }
      }
      &__hamburger {
        background: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        display: none;
        &--line {
          width: 3em;
          height: 0.3em;
          background-color: $color-primary;
          margin: 0.7em;
          border-radius: 20px;
          transition: 0.3s ease-in-out;
        }
        @include mobile {
          display: block;
          transition: 0.3s ease-in-out;
          &--active {
            .nav__hamburger--line:first-child {
              transform: translateY(-999px);
            }
            .nav__hamburger--line:nth-child(2) {
              transform: rotate(-405deg) translateY(10px);
            }
            .nav__hamburger--line:nth-child(3) {
              transform: rotate(45deg) translateY(-10px);
            }
          }
        }
      }
    }
  }
}
